export default {
	data() {
		return {
			imgface:null
		}
	},
	methods: {
		cxxz() {
			uni.chooseImage({
				count:1,
				sizeType:["compressed"],
				sourceType:["album"],
				success:(res) =>{
					let tempFilePaths =res.tempFilePaths[0];
					this.imgface = tempFilePaths;
				}
			})
			
		},
		toshangchuan(){
			uni.showLoading({
				title:"图片上传中..."
			})
			let userinfo= uni.getStorageSync("globalinfo");
			uni.uploadFile({
				url:"https://www.imovietrailer.com/superhero/user/uploadFace?userId="+userinfo.id+"&qq=3129996800",
				method:"POST",
				filePath:this.imgface,
				name:"file",
				header:{
					"headerUserId":userinfo.id,
					"headerUserToken":userinfo.userUniqueToken
				},
				success:(res) => {
					//console.log(typeof(res.data));
					
					let resdata=JSON.parse(res.data);
					console.log(resdata);
					if(resdata.status == 200){
						uni.setStorageSync("globalinfo",resdata.data);
						uni.navigateBack({
							delta:1
						})							
						
					}else if(resdata.status != 200){
						uni.showToast({
							title:resdata.msg,
							image:"/static/icos/error.png"
						})

					}
		
				},
				complete: () => {
					uni.hideLoading()
				}
				
			})
		}
	},
	onLoad(option) {
		this.imgface = option.tempFilePaths;
	}
}
